//import Grouper from "./grouper";

export default class InputHandler {
    constructor(game, grouper, gamestate) {
        //
        this.lastMouse;
        this.loadPrimed = false;
        this.gamestate = gamestate;
        this.lastCode = 0;
        //
        document.addEventListener("keydown", event => {
            switch (event.keyCode) {
                case 32:
                    //console.log("Space registered");
                    if(game.moving == game.inSize && game.gamestate == 0){
                        game.viewMode = (-1 * game.viewMode) + 1;
                        grouper.identify();
                        grouper.selectGroup(this.lastMouse);
                    }
                    break;
                //
                case 13:
                    game.logInfo();
                    if(game.gamestate == 1 || game.gamestate == 3){
                        game.level++;
                        console.log(`Enter registered, level: ${game.level}`);
                        game.loadLevel(game.level);
                    }else if (game.gamestate == 2){
                        game.gamestate = 3;
                        console.log("Win screen overridden.");
                    }
                    break;
                case 76:
                    this.loadPrimed = true;
                    console.log("Load primed");
                    break;
                case 8:
                    game.loadLevel(game.level);
                case 82:
                    if(this.lastCode == 67){
                        game.startCreation();
                    }
                    break;
                default:
                    if(0 <= event.key && event.key <= 9 && this.loadPrimed){
                        game.loadLevel(event.key);
                        this.loadPrimed = false;
                    }
                    break;
            }
            this.lastCode = event.keyCode;
        });
        //
        document.addEventListener("mousemove", event => {
            if(game.gamestate == 0){
                if(game.moving == game.inSize){
                    grouper.selectGroup(event);
                    this.lastMouse = event;
                }else{
                    game.moveGroup(event);
                }
            }
        });
        //
        document.addEventListener("mousedown", event => {
            if(grouper.selectedGroup != game.inSize && game.gamestate == 0){
                game.startGroupMove(event);
            }else if(game.gamestate == 1 || game.gamestate == 3){
                game.menu.checkNext(event);
            }else if (game.gamestate == 2){
                game.gamestate = 3;
                console.log("Win screen overridden.");
            }
        });
        //
        document.addEventListener("mouseup", event => {
            if(grouper.selectedGroup != game.inSize){
                game.stopGroupMove();
            }
        });
    }
}
